<template>
  <div>
    <v-container class="terapeutas__container">
      <title-ribbon
        :title="
          data_sucursal && data_especialidad
            ? data_especialidad.Nombre + ' ' + data_sucursal.Nombre
            : `...`
        "
      />
      <v-row class="terapeutas-row__inputs">
        <v-col class="col-12 tri-main-col">
          <div class="tri-title">
            <h4>Buscar por:</h4>
          </div>
          <div class="tri-group">
            <div class="tri-item">
              <label class="grey--text">
                Nombre del Terapeuta
              </label>
              <v-text-field
                v-model="nombre"
                class="tri-input"
                clearable
                outlined
                dense
              />
            </div>
            <div class="tri-item">
              <label class="grey--text">
                Especialidad del Terapeuta
              </label>
              <v-select
                v-model="tolink_especialidad"
                class="tri-input"
                :items="especialidades"
                item-value="ToLink"
                item-text="NombreCompleto"
                clearable
                outlined
                dense
              />
            </div>
            <div class="tri-item">
              <label class="grey--text">
                Edad del Terapeuta
              </label>
              <v-select
                v-model="edad"
                :items="[
                  { Value: null, Text: 'Todos' },
                  { Value: 0, Text: 'Menor de 40 años' },
                  { Value: 1, Text: 'Mayor de 40 años' },
                ]"
                class="tri-input"
                item-text="Text"
                item-value="Value"
                outlined
                dense
              />
            </div>
          </div>
          <div class="tri-group">
            <div class="tri-item">
              <label class="grey--text">
                Sexo del Terapeuta
              </label>
              <v-select
                v-model="sexo"
                :items="[
                  { Value: null, Text: 'Todos' },
                  { Value: 0, Text: 'Femenino' },
                  { Value: 1, Text: 'Masculino' },
                ]"
                class="tri-input"
                item-text="Text"
                item-value="Value"
                outlined
                dense
              />
            </div>
            <div class="tri-item">
              <label class="grey--text">
                Rango Etario Pacientes
              </label>
              <v-combobox
                v-model="rango_etario"
                :items="data_sucursal.RangosEtarios"
                class="tri-combobox"
                item-text="NombreRangoEdad"
                item-value="CodRangoEdad"
                outlined
                chips
                dense
              />
            </div>
          </div>
        </v-col>
        {{ $route.query.cod_terapeuta }}
      </v-row>
      <v-row class="terapeutas-row__tolmsg">
        <v-col class="trt-main-col">
          <span>
            Si necesita saber nuestros profesionales que atienden en Terapia
            Online, puede verlos
            <a
              href="https://terapiaonline.cl/online/home/terapeutas"
              target="_blank"
            >aquí</a>
          </span>
        </v-col>
      </v-row>
      <splitter />
      <v-row class="terapeutas-row__boxes">
        <v-col
          v-for="(terapeuta, i) in data_terapeutas"
          :key="i"
          class="col-12 col-sm-6 col-md-4 col-lg-3 trb-card"
        >
          <div
            class="card-main"
            @click="minicv(terapeuta.CodTerapeuta)"
          >
            <v-row class="card-main-row">
              <v-col class="col-12 cm_datos">
                <div class="cm_datos_up">
                  <div
                    v-lazy-container="{
                      selector: 'img',
                      error: `https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto,q_auto,c_scale/fotos/default.png`,
                      loading: `https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto,q_auto,c_scale/fotos/default.png`,
                      attempt: 3,
                    }"
                    class="cm_datos_imagen"
                  >
                    <img
                      class="imgen"
                      :data-src="
                        `https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto,q_auto,c_scale/fotos/${terapeuta.Rut}.png`
                      "
                    >
                  </div>
                  <div class="cm_datos_text">
                    <h3>
                      {{ terapeuta.Nombres }}
                      <h4 v-if="terapeuta.tolAble">
                        (con Terapia Online)
                      </h4>
                    </h3>
                    <div class="parrafos">
                      <p>
                        {{ terapeuta.Titulo }} <br>
                        {{ terapeuta.Universidad }}
                      </p>
                      <p>
                        {{ terapeuta.Especializaciones }}
                      </p>
                    </div>
                  </div>
                </div>
                <div class="cm_datos_down">
                  <v-btn
                    class="success"
                    :small="$vuetify.breakpoint.xs"
                  >
                    Leer más
                  </v-btn>
                </div>
              </v-col>
            </v-row>
          </div>
        </v-col>
      </v-row>
      <v-row class="terapeutas-row__pagination">
        <v-col class="trb_main">
          <v-pagination
            v-if="total_terapeutas > limite"
            v-model="pagina"
            :length="Number(totalPaginas)"
            prev-icon="mdi-menu-left"
            next-icon="mdi-menu-right"
            previous-aria-label="ps"
            next-aria-label="sd"
            color="success"
          />
        </v-col>
        <mini-cv-dialog v-bind="{ value }" />
      </v-row>
    </v-container>
  </div>
</template>

<script>
  import Vue from 'vue'
  import { debounce } from '@/helpers'
  import { mapState } from 'vuex'

  export default {
    name: 'Terapeutas',
    components: {
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
      MiniCvDialog: () => import('@/components/web/base/MiniCvDialog'),
      Splitter: () => import('@/components/web/base/Splitter'),
    },
    data () {
      return {
        // sucursal_to_link: this.$route.params.sucursal,
        // especialidad_to_link: this.$route.params.especialidad,
        // cod_especialidad: null,
        cod_terapeuta: this.$route.query.cod_terapeuta ? this.$route.query.cod_terapeuta : null, // nuevo
        tolink_especialidad: '',
        rango_etario: null,
        sexo: null,
        edad: null,
        nombre: this.$route.params.nombre ? this.$route.params.nombre : null,
        pagina: 1,
        limite: 100, // Lo dejamos en 100 para no quitar la paginación. Modificarlo si es necesario reincorporarla
        totalPaginas: '',
        data_sucursal: '',
        data_especialidad: '',
        data_terapeutas: '',
        total_terapeutas: '',
        especialidades: [],
        charged: true,
        value: {
          active: false,
          terapeuta: '',
          sucursal: null,
        },
      }
    },
    computed: {
      ...mapState('web', ['problemasTiposMenu']),
    },
    watch: {
      nombre: debounce(function () {
        this.obtenerTerapeutas(true)
      }, 1500),
      edad: debounce(function () {
        this.obtenerTerapeutas(true)
      }, 1500),
      sexo: debounce(function () {
        this.obtenerTerapeutas(true)
      }, 1500),
      rango_etario: debounce(function () {
        this.obtenerTerapeutas(true)
      }, 1500),
      pagina: function () {
        this.obtenerTerapeutas()
      },
      tolink_especialidad: function (val) {
        // Si se cambia la especialidad en el select, cambia a nueva ruta y carga terapeutas y titulo
        this.cambiarEspecialidad(val)
      },
    },
    created () {
      this.obtenerDataSucursal()
      this.obtenerDataEspecialidad()
      this.obtenerTerapeutas()
      this.obtenerEspecialidadesBySucursal()
    // this.print()
    },
    methods: {
      obtenerTerapeutas (bySearch = false) {
        if (bySearch) {
          this.pagina = 1
        }
        if (this.$route.params.especialidad && this.$route.params.especialidad.indexOf('-infantil') > -1 && this.rango_etario === null) {
          this.rango_etario = { NombreRangoEdad: 'NIÑO', CodRangoEdad: 1 }
        }
        const desde = (this.pagina - 1) * this.limite
        const body = {
          sucursal_to_link: this.$route.params.sucursal,
          especialidad_to_link: this.$route.params.especialidad,
          ct: this.$route.query.ct, /* CodTerapeuta */
          cod_especialidad: this.cod_especialidad,
          rango_etario: this.rango_etario,
          sexo: this.sexo,
          edad: this.edad,
          nombre: this.nombre,
          desde: desde,
          limite: this.limite,
        }
        // console.log(body)
        Vue.axios
          .post('/api/terapeutas', body)
          .then(response => {
            this.data_terapeutas = response.data.result
            this.total_terapeutas = response.data.total
            this.totalPaginas = Math.round(response.data.total / this.limite)
          })
          .catch(error => {
            console.log(error)
          })
      },
      obtenerDataSucursal () {
        const body = {
          addRangosEtarios: true,
        }
        Vue.axios
          .post(`/api/sucursales/${this.$route.params.sucursal}`, body)
          .then(response => {
            this.data_sucursal = response.data.result[0]
          })
          .catch(error => {
            console.log(error)
          })
      },
      obtenerDataEspecialidad () {
        const body = {
          // Por ahora no agregamos metatags
          addMeta: false,
        }
        Vue.axios
          .post(`/api/especialidades/${this.$route.params.especialidad}`, body)
          .then(response => {
            this.data_especialidad = response.data.result[0]
          })
          .catch(error => {
            console.log(error)
          })
      },
      obtenerEspecialidadesBySucursal () {
        Vue.axios
          .get('/api/especialidades/combos', {
            params: {
              toLink: this.$route.params.sucursal,
            },
          })
          .then(response => {
            this.especialidades = response.data.result
          })
          .catch(err => {
            console.log(err)
          })
      },
      cambiarEspecialidad (val) {
        this.$router.push(`/terapeutas/${this.$route.params.sucursal}/${val}`)
        this.obtenerTerapeutas()
        this.obtenerDataEspecialidad()
      },
      print () {
        console.log(this.problemasTiposCombo)
      },
      minicv (cod_terapeuta) {
        const body = {
          sucursal_to_link: this.$route.params.sucursal,
          cod_terapeuta,
          addProblemas: true,
          addRangoEtario: true,
        }
        Vue.axios
          .post('/api/terapeutas/terapeuta', body)
          .then(response => {
            this.value.terapeuta = response.data.result[0]
            this.value.active = true
            this.value.sucursal = response.data.sucursal
          // console.log(this.value.terapeuta)
          })
          .catch(error => {
            console.log(error)
          })
      },
    },
    metaInfo: {
      title: 'Terapeutas',
      titleTemplate: '%s - Centro de Terapia del Comportamiento',
      htmlAttrs: {
        lang: 'es',
        amp: true,
      },
    },
  }
</script>
